.header{
  width: 1366px;
}
.nav_top{
  margin-top: 61px;
  height: 40px;
  border: 1px solid #000000;
  background-color: #343434;
  box-shadow: #000000 25%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;
  filter: drop-shadow(0px 4px 4px #000000);
}
.nav_top_left{
  margin-left: 98px;
  display: flex;
  gap: 23px;
}
.nav_right{
  margin-right: 98px;
}

.nav_top_a{
  text-decoration: none;
  color:  #FFFFFF;
  
}
.nav_botom{
  height: 65px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
 
}
.logo_img{
  margin-left: 98px;
  width: 70px;
  height: 17px;
  filter: drop-shadow(0px 4px 4px #000000 25%);
}
.nav{
  height: 11px;
  display: flex;
  align-items: center;
  gap: 30px;
}
.nav_botom_a{
  text-decoration: none;
  color:#343434
}
.nav_botom_a:hover{
  color: #626262;
  text-shadow:#000000 1px 0px, #000000 1px 1px, #000000 0px 1px, #000000 -1px 1px, #000000 -1px 0px, #000000 -1px -1px, #000000 0px -1px, #000000 1px -1px;
  filter: drop-shadow(0px 2px 4px #000000 );
}
.search{
  display: flex;
  align-items: center;
  gap: 20px;
  margin-right: 98px;
}
.search_form{
  position: relative;
  width: 282px;
  height: 40px;
  border-radius: 15px;
  background: #E8E8E8;
  transition: all 0.3s ease;
}

.search_input{
  position: absolute;
  top: 10px;
  left: 38px;
  font-size: 14px;
  background: #E8E8E8;
  color: black;
  width: 195px;
  height: 20px;
  border: none;
  appearance: none;
  outline: none;
}
.search_button{
  position: absolute;
  top: 9px;
  left: 9px;
  height: 21.5px;
  width: 21.5px;
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  outline: none!important;
  cursor: pointer;
}
.heart{
  width: 24px;
  height: 24px;
}
.basket{
  width: 24px;
  height: 24px;
}
.search_input:-webkit-autofill,
.search_input:-webkit-autofill:hover, 
.search_input:-webkit-autofill:focus
{
  -webkit-text-fill-color: black;
  -webkit-box-shadow: 0 0 0px 1000px #E8E8E8 inset;
  transition: background-color 5000s ease-in-out 0s;
}